    import React, {  useEffect, useState } from "react";
    import { useAuth } from "@/hooks/useAuth";
    import { User, LogOut, Sparkles, Shield } from "lucide-react";

    const Navbar = () => {
    const { user, logout, currentUser } = useAuth();
    const [isScrolled, setIsScrolled] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
        setIsScrolled(window.scrollY > 20);
        };
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    useEffect(() => {
        const checkAuth = async () => {
        const token = localStorage.getItem("auth-token");
        console.log(token);
        if (!token) {
            user(null);
            return;
        }
        try {
            const res = await fetch("/api/auth/verify", {
            method: "GET",
            headers: {
                Authorization: `Bearer ${token}`,
                "Content-Type": "application/json",
            },
            });

            if (res.ok) {
            const data = await res.json();
            user(data.user);
            } else {
            localStorage.removeItem("auth-token");
            user(null);
            }
        } catch (error) {
            localStorage.removeItem("auth-token");
            user(null);
        }
        };
        checkAuth();
    }, []);

    return (
        <>
        {/* Animated background particles */}
        <div className="fixed top-0 left-0 w-full h-20 pointer-events-none z-40">
            <div className="absolute top-2 left-1/4 w-1 h-1 bg-purple-400 rounded-full animate-pulse opacity-60"></div>
            <div className="absolute top-6 right-1/3 w-1 h-1 bg-pink-400 rounded-full animate-pulse opacity-40" style={{ animationDelay: '1s' }}></div>
            <div className="absolute top-4 left-2/3 w-1 h-1 bg-blue-400 rounded-full animate-pulse opacity-50" style={{ animationDelay: '2s' }}></div>
        </div>

        <nav className={`fixed top-0 left-0 right-0 z-50 transition-all duration-500 ${
            isScrolled 
            ? 'bg-black/95 backdrop-blur-xl border-b border-purple-500/30 shadow-lg shadow-purple-500/10' 
            : 'bg-black/70 backdrop-blur-lg border-b border-purple-500/20'
        }`}>
            {/* Animated top border */}
            <div className="absolute top-0 left-0 right-0 h-0.5 bg-gradient-to-r from-transparent via-purple-500 via-pink-500 to-transparent animate-pulse"></div>
            
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="flex justify-between items-center h-16">
                {/* Enhanced Logo Section */}
                <div className="flex-shrink-0 group relative">
                <div className="absolute -inset-2 bg-gradient-to-r from-purple-600/20 to-pink-600/20 rounded-xl blur opacity-0 group-hover:opacity-100 transition-all duration-500"></div>
                <div className="relative flex items-center space-x-3">
                    <img
                    className="h-10 w-auto transition-all duration-500 group-hover:scale-110 group-hover:drop-shadow-lg group-hover:brightness-125 group-hover:saturate-150"
                    src="https://cdn.logojoy.com/wp-content/uploads/2018/05/01112536/5_big12.png"
                    alt="Logo"
                    />
                    <Sparkles className="w-5 h-5 text-purple-400 opacity-0 group-hover:opacity-100 transition-all duration-500 group-hover:rotate-180" />
                </div>
                </div>

                {/* Enhanced Navigation Buttons */}
                <div className="flex items-center space-x-6">
                {!currentUser && (
                    <>
                    <a
                        href="/login"
                        className="group relative px-8 py-3 text-white font-semibold transition-all duration-500 hover:scale-105 hover:-translate-y-1 overflow-hidden"
                    >
                        {/* Animated background layers */}
                        <div className="absolute inset-0 bg-gradient-to-r from-purple-600/10 to-pink-600/10 rounded-xl opacity-0 group-hover:opacity-100 transition-all duration-500"></div>
                        <div className="absolute inset-0 bg-gradient-to-r from-transparent via-purple-500/20 to-transparent rounded-xl transform -skew-x-12 scale-x-0 group-hover:scale-x-100 transition-transform duration-700"></div>
                        <div className="absolute inset-0 border border-purple-500/0 group-hover:border-purple-500/50 rounded-xl transition-all duration-500"></div>
                        
                        <span className="relative z-10 flex items-center space-x-2 group-hover:text-purple-300 transition-colors duration-300">
                        <Shield className="w-4 h-4 opacity-0 group-hover:opacity-100 transition-all duration-300" />
                        <span>Login</span>
                        </span>
                        
                        {/* Glow effect */}
                        <div className="absolute inset-0 rounded-xl opacity-0 group-hover:opacity-100 transition-opacity duration-500 bg-gradient-to-r from-purple-600/20 to-pink-600/20 blur-xl"></div>
                    </a>

                    <a
                        href="/register"
                        className="group relative px-8 py-3 bg-gradient-to-r from-purple-600 via-pink-600 to-purple-700 text-white font-semibold rounded-xl transition-all duration-500 hover:scale-105 hover:-translate-y-1 hover:shadow-2xl hover:shadow-purple-500/30 active:scale-95 overflow-hidden"
                    >
                        {/* Multiple animated layers */}
                        <div className="absolute inset-0 bg-gradient-to-r from-pink-600 via-purple-600 to-blue-600 rounded-xl opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>
                        <div className="absolute inset-0 bg-gradient-to-r from-purple-600/50 to-pink-600/50 rounded-xl opacity-0 group-hover:opacity-100 group-hover:scale-110 transition-all duration-700"></div>
                        <div className="absolute -inset-1 bg-gradient-to-r from-purple-600 to-pink-600 rounded-xl opacity-0 group-hover:opacity-50 blur transition-all duration-500"></div>
                        
                        <span className="relative z-10 flex items-center space-x-2 group-hover:scale-105 transition-transform duration-300">
                        <Sparkles className="w-4 h-4 group-hover:rotate-180 transition-transform duration-500" />
                        <span>Register</span>
                        </span>
                    </a>
                    </>
                )}

                {currentUser && (
                    <>
                    <button
                        onClick={() => {
                        logout();
                        // router.push("/");
                        }}
                        className="group relative px-8 py-3 bg-gradient-to-r from-red-600 via-pink-600 to-red-700 text-white font-semibold rounded-xl transition-all duration-500 hover:scale-105 hover:-translate-y-1 hover:shadow-2xl hover:shadow-red-500/30 active:scale-95 overflow-hidden"
                    >
                        <div className="absolute inset-0 bg-gradient-to-r from-pink-600 via-red-600 to-orange-600 rounded-xl opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>
                        <div className="absolute -inset-1 bg-gradient-to-r from-red-600 to-pink-600 rounded-xl opacity-0 group-hover:opacity-50 blur transition-all duration-500"></div>
                        
                        <span className="relative z-10 flex items-center space-x-2 group-hover:scale-105 transition-transform duration-300">
                        <LogOut className="w-4 h-4 group-hover:-translate-x-1 transition-transform duration-300" />
                        <span>Logout</span>
                        </span>
                    </button>

                    <button className="group relative p-3 text-white hover:text-purple-300 transition-all duration-500 hover:scale-110 hover:-translate-y-1 rounded-xl overflow-hidden">
                        <div className="absolute inset-0 bg-gradient-to-r from-purple-600/20 to-pink-600/20 rounded-xl opacity-0 group-hover:opacity-100 transition-all duration-500"></div>
                        <div className="absolute inset-0 border border-purple-500/0 group-hover:border-purple-500/50 rounded-xl transition-all duration-500"></div>
                        <div className="absolute -inset-1 bg-gradient-to-r from-purple-600/30 to-pink-600/30 rounded-xl opacity-0 group-hover:opacity-100 blur transition-all duration-500"></div>
                        
                        <User className="relative z-10 w-6 h-6 group-hover:rotate-12 group-hover:scale-110 transition-all duration-500" />
                    </button>
                    </>
                )}
                </div>
            </div>
            </div>

            {/* Enhanced bottom border with animation */}
            <div className="absolute bottom-0 left-0 right-0 h-px">
            <div className="absolute inset-0 bg-gradient-to-r from-transparent via-purple-500 via-pink-500 to-transparent opacity-70"></div>
            <div className="absolute inset-0 bg-gradient-to-r from-transparent via-blue-400 to-transparent opacity-30 animate-pulse"></div>
            </div>

            {/* Enhanced corner indicators */}
            <div className="absolute top-2 left-4 flex space-x-1">
            <div className="w-2 h-2 bg-green-400 rounded-full animate-pulse shadow-lg shadow-green-400/50"></div>
            <div className="w-1 h-1 bg-green-300 rounded-full animate-pulse opacity-60" style={{ animationDelay: '0.3s' }}></div>
            </div>
            <div className="absolute top-2 right-4 flex space-x-1">
            <div className="w-1 h-1 bg-red-300 rounded-full animate-pulse opacity-60" style={{ animationDelay: '0.8s' }}></div>
            <div className="w-2 h-2 bg-red-400 rounded-full animate-pulse shadow-lg shadow-red-400/50" style={{ animationDelay: '0.5s' }}></div>
            </div>

            {/* Additional ambient lighting effects */}
            <div className="absolute top-0 left-1/4 w-32 h-32 bg-purple-500/5 rounded-full blur-3xl animate-pulse"></div>
            <div className="absolute top-0 right-1/4 w-32 h-32 bg-pink-500/5 rounded-full blur-3xl animate-pulse" style={{ animationDelay: '1s' }}></div>
        </nav>
        </>
    );
    };

    export default Navbar;